@import '~@/styles/var.scss';

.loading-box {
  position: relative;
  width: 100%;
  padding: 80px 0;
  overflow: hidden;
  text-align: center;
  .loading {
    position: relative;
    display: inline-block;
    height: 50px;
    padding-left: 60px;
    line-height: 50px;
    font-size: 36px;
    color: $loading-color;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 45px;
      height: 45px;
      background: url('data:image/gif;base64,')
        no-repeat center/45px;
    }
  }
}
